.about {
  display: flex;
  min-height: 100vh;
  padding-top: 300px;
  background-image: url("../images/decor-expert1.svg");
  background-position: center right 30%;
  background-size: cover;
  background-repeat: no-repeat;

  &__wrapper {
    display: flex;
  }

  &__head {
    margin-right: 50px;
    width: 100%;
    max-width: 460px;
  }

  h1 {
    width: 460px;
    font-family: "Neue Machina", sans-serif;
    text-transform: uppercase;

    span {
      color: $main;
    }
  }

  h4 {
    font-weight: 500;
    font-size: 48px;
    text-transform: uppercase;
    margin-top: 120px;

    span {
      color: $main;
    }
  }

  &__box {
    max-width: 970px;

    p {
      font-size: 24px;

      &:not(:last-child) {
        margin-bottom: 22px;
      }

    }
  }
}

.video {
  color: $white;
  padding: 234px 0 217px;
  background-image: url("../images/video-bg.jpg");
  background-position: center;
  background-size: cover;
  margin-bottom: 150px;

  &__wrapper {
    display: flex;
    align-items: center;
  }

  &__inner {
    max-width: 740px;

    h4 {
      font-weight: 500;
      font-size: 48px;
      margin-bottom: 25px;
    }

    p {
      font-weight: 500;
      font-size: 36px;
    }
  }

  &__play {
    margin: 0 auto;

    button {
      margin-bottom: 25px;
      background-color: transparent;
      padding: 0;
      border: none;

      img {
        border-radius: 50%;
        cursor: pointer;
      }
    }

    span {
      font-size: 24px;
      text-transform: uppercase;
      display: block;
      text-align: center;
    }
  }
}

.we__about {
  margin-bottom: 150px ;
  h2 {
    &::after {
      display: none;
    }
  }
}
.we-do__about {
  margin-bottom: 186px;
}

.we-box__about {
  p {
    font-size: 24px;
    margin-bottom: 0;
  }
}

.team {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  li {
    max-width: 460px;
    width: 100%;
    margin-bottom: 64px;

    img {
      object-fit: cover;
      margin-bottom: 25px;
    }
    h5 {
      font-weight: 400;
      font-size: 24px;
      margin-bottom: 10px;
    }
    p {
      color: $secondary;
    }
  }
}

.join {
  margin-bottom: 250px;
  background-image: linear-gradient(88.28deg, #AC1DEF -0.15%, #1D4BEF 100%);
  h2 {
    text-transform: uppercase;
    color: $white;
    line-height: 90px;
    text-align: center;
    margin-right: 66px;
  }
  a {
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 80px 0;
    height: 100%;
  }
}

.facts {
  display: flex;
  margin-bottom: 200px;
  h2 {
    max-width: 460px;
    width: 100%;
    margin-right: 50px;
  }
  span {
    color: $main;
  }
  ul {
    li {
      font-size: 24px;
      line-height: 40px;
      padding-left: 55px;
      position: relative;
      &::after {
        position: absolute;
        left: 0;
        top: 0;
        content: url("../images/blue-star.svg");
        width: 40px;
        height: 40px;
      }
      &:not(:last-child) {
        margin-bottom: 50px;
      }
    }
  }
}